import logo from './logo.svg';
import './App.css';
import { Button, ButtonProps } from "@material-ui/core/";
import DirectoryCreateForm from './components/DirectoryCreateForm';
import OutlinedButtons from './components/Button'
import SimpleCard from './components/Card';
//import FullScreenDialog from './components/Directorydialog';
import { connect } from 'react-redux';
import { Component } from 'react';
import Directorylist from './components/Directorylist';


class Main extends Component{
  constructor(props) {
    super(props);
  }

  render(){
    return (
      <div className="App">
      <h1 class="text-center mt-5 mb-4"><span class="marker-animation">リストの作成</span></h1>
      <DirectoryCreateForm />
      <a id="directory"></a>
      <h1 class="text-center mt-5 mb-4"><span class="marker-animation">リスト一覧</span></h1>
      <Directorylist />
      {/*<SimpleCard />*/}
      </div>
    );
  }
}

export default connect((state)=>state)(Main);
