import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';

import ShowChartIcon from '@material-ui/icons/ShowChart';
import HomeIcon from '@material-ui/icons/Home';
import SaveAltIcon from '@material-ui/icons/SaveAlt';
import LinkIcon from '@material-ui/icons/Link';

const useStyles = makeStyles({
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
});

export default function SwipeableTemporaryDrawer() {
  const classes = useStyles();
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List >
      <ListItem button key="Top" onClick={()=>(window.location.href="")}>
        <ListItemIcon><HomeIcon /></ListItemIcon>
        <ListItemText primary="Top" />
      </ListItem>
      <ListItem button key="How to USE" onClick={()=>(window.location.href="#howto")}>
        <ListItemIcon><ShowChartIcon /></ListItemIcon>
        <ListItemText primary="How to USE" />
      </ListItem>
      <ListItem button key="Directory" onClick={()=>(window.location.href="#directory")}>
        <ListItemIcon><SaveAltIcon /></ListItemIcon>
        <ListItemText primary="List" />
      </ListItem>
      <ListItem button key='Producer' onClick={()=>(window.open("https://hirokazuniimoto.github.io/portfolio/", '_blank'))}>
        <ListItemIcon><LinkIcon /></ListItemIcon>
        <ListItemText primary='Producer' />
      </ListItem>
        {/*
          {['Top', 'How to USE', 'Directory', 'Producer'].map((text, index) => (
            <ListItem button key={text} onClick={()=>(window.open("http://google.com", '_blank'))}>
              <ListItemIcon>{index % 2 === 0 ? <HomeIcon /> : <ShowChartIcon />}</ListItemIcon>
              <ListItemText primary={text} />
            </ListItem>
          ))}
        */}
      </List>
      {/*
        <List>
          {['All mail', 'Trash', 'Spam'].map((text, index) => (
            <ListItem button key={text}>
              <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
              <ListItemText primary={text} />
            </ListItem>
          ))}
        </List>
      */}
      <Divider />

    </div>
  );

  return (
    <div>
    {['left'].map((anchor) => (
      <React.Fragment key={anchor}>
        {/*<Button onClick={toggleDrawer(anchor, true)}>{anchor}</Button>*/}
        <IconButton
          edge="start"
          className={classes.menuButton}
          color="inherit"
          aria-label="open drawer"
          onClick={toggleDrawer(anchor, true)}
        >
          <MenuIcon />
        </IconButton>
        <SwipeableDrawer
          anchor={anchor}
          open={state[anchor]}
          onClose={toggleDrawer(anchor, false)}
          onOpen={toggleDrawer(anchor, true)}
        >
          {list(anchor)}
        </SwipeableDrawer>
      </React.Fragment>
    ))}
    </div>
  );
}
