class Directorylist extends Component {
  inputtitle = '';
  inputurl = '';
  //data = [];
  data={};

  style={
    appBar: {
      position: 'relative',
    },
    title: {
      //marginLeft: theme.spacing(2),
      flex: 1,
    },
  }
  constructor(props) {
    super(props);
    this.state = {
      open:false,
      dirnametitle:"",
      pages:[],
      dirnames:this.getdirnames(),
      urltitle:this.geturltitle(),
      urlerrorflag:false,
    }
    this.doChange1 = this.doChange1.bind(this);
    this.doChange2 = this.doChange2.bind(this);
    this.doAction = this.doAction.bind(this);
    this.doDelete = this.doDelete.bind(this);
    this.deletedirectory = this.deletedirectory.bind(this);
    this.getdirnames = this.getdirnames.bind(this);
    this.geturltitle = this.geturltitle.bind(this);
  }

  getdirnames(){
    let dirnames = [];
    for( let i = 0; i < localStorage.length; i++ ){
       var key = localStorage.key(i);
       if(localStorage.getItem(key).indexOf("newdir")!=-1){
         dirnames.push(key)
       }
    }
    return dirnames;
  }

  geturltitle(dirname){
    try {
      this.pages = localStorage.getItem(dirname).split(',');
      this.pages.pop();
    } catch (e) {
      this.pages = [];
    }
    return this.pages;
  }


  doChange1(e){
    this.inputtitle = e.target.value;
    this.setState({
    });
  }

  doChange2(e){
    this.inputurl = e.target.value;
    this.setState({
    });
  }

  doAction(e){
    if(localStorage.hasOwnProperty(this.inputtitle) || this.inputtitle=="" || this.inputtitle=="newdir"){
      this.setState({
        alertflag:false,
        erroralertflag:true,
        dname:"",
      });
      return;
    }else{
      this.setState({
        title: this.inputtitle,
        url: this.inputurl,
        alertflag:true,
        erroralertflag:true,
        dname:"",
        dirnames:this.getdirnames(),
        //pages:this.geturltitle(this.state.dirnametitle),
      });
      console.log(this.state.pages);
      this.data=[this.inputtitle,this.inputurl];
      this.data=[this.inputtitle];
      this.value = localStorage.getItem(this.state.dirnametitle);
      this.data.push(this.value);
      //localStorage.setItem(this.state.dirnametitle,this.data);
      localStorage.setItem(this.state.dirnametitle,this.data);
      localStorage.setItem(this.inputtitle,this.inputurl);

      try {
        this.state.pages = localStorage.getItem(this.state.dirnametitle).split(',');
        this.state.pages.pop();
      } catch (e) {
        this.state.pages = [];
      }
      //localStorage.setItem(this.state.dirnametitle, JSON.stringify(this.data));
    }
    e.preventDefault();
  }

  doDelete(page){
    localStorage.removeItem(page);
    this.value=localStorage.getItem(this.state.dirnametitle);
    page=page+",";
    this.value=this.value.replace(page,"");
    localStorage.setItem(this.state.dirnametitle,this.value);
    try {
      this.state.pages = localStorage.getItem(this.state.dirnametitle).split(',');
      this.state.pages.pop();
    } catch (e) {
      this.state.pages = [];
    }
    this.setState({
    });
  }

  deletedirectory(e){
    this.result = window.confirm("本当に消去してもよろしいですか？")
    if(this.result){
      this.deletepages = localStorage.getItem(this.state.dirnametitle).split(',');
      this.deletepages.pop();
      this.deletepages.map((deletepage)=>{
        localStorage.removeItem(deletepage);
      })
      localStorage.removeItem(this.state.dirnametitle);
      this.setState({
        open:false,
      });
      window.location.reload();
    }
  }

  setOpen(condition){
    this.setState({
      open:condition,
    });
  }

  urlopen(url){
    if(url.indexOf('http://') != -1 || url.indexOf('https://') != -1){
      window.open(url, '_blank');
    }else{
      this.setState({
        urlerrorflag:true,
      });
    }
  }

  handleClickOpen(dirname) {
    this.setState({
      open:true,
      dirnametitle:dirname,
    });
    //this.state.pages = JSON.parse(localStorage.getItem(this.state.dirnametitle));
    try {
      this.state.pages = localStorage.getItem(dirname).split(',');
      this.state.pages.pop();
    } catch (e) {
      this.state.pages = [];
    }
    //console.log(this.state.pages);

  };

  handleClose = () => {
    this.setOpen(false);
  };



  render(){
    return (
      <div>
      {this.state.dirnames.map((dirname) =>
        <div>
        <Button variant="outlined" color="primary" onClick={() => this.handleClickOpen(dirname)} style={{width:"80%",height:"80px",fontSize:"20px",marginTop:"20px"}}>
          {dirname}
        </Button>

        </div>
      )}
      <Dialog fullScreen open={this.state.open} onClose={this.handleClose} TransitionComponent={Transition}>
        <AppBar style={this.style.appBar}>
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={this.handleClose} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" style={this.style.title}>
              {this.state.dirnametitle}
            </Typography>
            <Button autoFocus color="inherit" onClick={this.deletedirectory}>
              リスト消去
            </Button>
            <Button autoFocus color="inherit" onClick={this.handleClose}>
              保存
            </Button>
          </Toolbar>
        </AppBar>
        {this.state.alertflag ?
          <Alert severity="success" style={{width:"100%",margin:"10 auto",marginTop:"20px"}}>新規ページ「{this.state.title}」を追加しました。</Alert>
          :(this.state.erroralertflag ?
          <Alert severity="error" style={{width:"100%",margin:"10 auto",marginTop:"20px"}}>同名のページタイトルが存在するかページタイトルに問題があります。</Alert>
          :<p></p>)
        }

        {this.state.urlerrorflag ?
          <Alert severity="error" style={{width:"100%",margin:"10 auto",marginTop:"20px"}}>ページを開けませんでした。入力されたURLが間違っていないか確認してください。</Alert>
          :<p></p>
        }


        <div class="ml-3 mr-3 mt-4" style={{}}>
        <h5>新しくページを追加</h5>
        <form class="mt-1" noValidate autoComplete="off">
          <TextField id="standard-basic" label="ページのタイトルを入力" onChange={this.doChange1} style={{width:"100%"}} />
        </form>
        <form class="mt-1" noValidate autoComplete="off">
          <TextField id="standard-basic" label="URLを入力" onChange={this.doChange2} style={{width:"100%"}}/>
        </form>
        <div class="mt-4 mb-5">
        <Button variant="outlined" color="primary" onClick={this.doAction} style={{width:"100%"}} >
          追加
        </Button>
        </div>
        </div>

        <List>
        <Divider />
        {
          this.state.pages.map((page) =>(
            <div>
            <ListItem>
              <ListItemText primary={page} secondary={localStorage.getItem(page)} />
              <a onClick={() => this.urlopen(localStorage.getItem(page))} ><OutlinedButtons text="開く" /></a>
              <a onClick={() => this.doDelete(page)}><OutlinedButtons text="消去"/></a>
            </ListItem>
            <Divider />
            </div>
          )
        )
        }

        </List>

      </Dialog>
      </div>
    );
  }
}

export default connect((state)=>state)(Directorylist);
