import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import OutlinedButtons from './Button'
import Button from '@material-ui/core/Button';

import { Component } from 'react';
import { connect } from 'react-redux';
import { createdirectory } from '../actions/directory';

import FullScreenDialog from './Directorydialog';
import Directorycreatebutton from './directorycreatebutton';

import Alert from '@material-ui/lab/Alert';

function mappingState(state){
  return state;
}

class DirectoryCreateForm extends Component {
  inputdname = '';
  constructor(props){
    super(props);
    this.state = {
      alertflag:false,
      erroralertflag:false,
    }
    this.doChange = this.doChange.bind(this);
    this.doAction = this.doAction.bind(this);
  }

  doChange(e){
    this.inputdname = e.target.value;
    this.setState({
      //directoryname: e.target.value
    });
  }

  newdata = {};
  doAction(e){
    if(localStorage.hasOwnProperty(this.inputdname) || this.inputdname==""){
      this.setState({
        alertflag:false,
        erroralertflag:true,
        dname:"",
      });
      return;
    }else{
      e.preventDefault();
      this.setState({
        directoryname: this.inputdname,
        alertflag:true,
        erroralertflag:true,
        dname:"",
      });
      //let action = createdirectory(this.state.alertflag);
      //this.props.dispatch({type:'DirectoryCreate'});
      //this.props.dispatch(action);
      localStorage.setItem(this.inputdname, "newdir");
      setTimeout(() => {
        window.location.reload(); // Execution 5sec
      }, 2000);
      }
  }

  render(){
    return (
      <div> {/*className={classes.root}*/}
      {this.state.alertflag ?
        <Alert severity="success" style={{width:"80%",margin:"0 auto"}}>新規リスト「{this.state.directoryname}」を作成しました。</Alert>
        :(this.state.erroralertflag ?
        <Alert severity="error" style={{width:"80%",margin:"0 auto"}}>同名のリストが存在するかリスト名に問題があります。</Alert>
        :<p></p>)
      }
      {alert}
      <form class="mt-4" noValidate autoComplete="off">
        <TextField id="standard-basic" label="ディレクトリ名を入力" onChange={this.doChange} style={{width:"80%"}} />
      </form>
      <div class="mt-3">
        {/*
          <Directorycreatebutton text="ディレクトリ作成" />
        */}
        <Button variant="outlined" color="primary" style={{width:"80%"}} onClick={this.doAction}>
          リスト作成
        </Button>
      </div>
      {/*<FullScreenDialog onClick={this.doAction}/>*/}
      </div>
    );
  }
}

DirectoryCreateForm = connect(mappingState)(DirectoryCreateForm);

export default connect((state)=>state)(DirectoryCreateForm);
