import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import ListItemText from '@material-ui/core/ListItemText';
import ListItem from '@material-ui/core/ListItem';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';

import SimpleCard from './Card.js';
import OutlinedButtons from './Button.js';

import TextField from '@material-ui/core/TextField';


const useStyles = makeStyles((theme) => ({
  appBar: {
    position: 'relative',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function FullScreenDialog() {
  //const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      {/*<SimpleCard onClick={handleClickOpen} /> className={classes.appBar}  className={classes.title} */}
      <Button variant="outlined" color="primary" onClick={handleClickOpen} style={{width:"80%",height:"80px",fontSize:"20px"}}>
        ディレクトリ名
      </Button>
      <Dialog fullScreen open={open} onClose={handleClose} TransitionComponent={Transition}>
        <AppBar>
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" >
              ディレクトリ名
            </Typography>
            <Button autoFocus color="inherit" onClick={handleClose}>
              すべて開く
            </Button>
            <Button autoFocus color="inherit" onClick={handleClose}>
              保存
            </Button>
          </Toolbar>
        </AppBar>
        <List>
          <ListItem>
            <ListItemText primary="ページのタイトル" secondary="ページのURL" />
            <OutlinedButtons text="開く"/>
            <OutlinedButtons text="消去"/>
          </ListItem>
          <Divider />
          <ListItem>
            <ListItemText primary="ページのタイトル" secondary="ページのURL" />
            <OutlinedButtons text="開く"/>
            <OutlinedButtons text="消去"/>
          </ListItem>
          <Divider />
          {/*
            <ListItem>
              <form noValidate autoComplete="off">
                <TextField id="standard-basic" label="ディレクトリ名を入力"  style={{width:"100%"}} />
              </form>
              <div class="mt-3">
              <Button variant="outlined" color="primary" style={{width:"80%"}} >
                ディレクトリ作成
              </Button>
              </div>
            </ListItem>
          */}
          <Divider />
          <ListItem button>
            <ListItemText primary="Default notification ringtone" secondary="Tethys" />
          </ListItem>
        </List>

        <div class="ml-3 mr-3 mt-5" style={{}}>
        <h5>新しくページを追加</h5>
        <form class="mt-1" noValidate autoComplete="off">
          <TextField id="standard-basic" label="ページのタイトルを入力"  style={{width:"100%"}} />
        </form>
        <form class="mt-1" noValidate autoComplete="off">
          <TextField id="standard-basic" label="URLを入力"  style={{width:"100%"}} />
        </form>
        <div class="mt-4 mb-5">
        <Button variant="outlined" color="primary" style={{width:"100%"}} >
          追加
        </Button>
        </div>
        </div>

      </Dialog>
    </div>
  );
}
