const initData = {
  data:{}, //
  message:'please type message:',
  mode:'default',
  fdata:[],
  alertflag:false,
  erroralertflag:false,
};

export function directory(state = initData, action) {
  switch (action.type) {
    case 'DirectoryCreate':
      return DirectoryCreateReduce(state, action);

    case 'DirectoryDELETE':
      return DirectoryDletedeleteReduce(state, action);

    case 'URLFIND':
      return URLFindReduce(state, action);

    case 'URLADD':
      return URLAddReduce(state, action);

    case 'URLDELETE':
      return URLDleteReduce(state, action);

    default:
      return state;
  }
}


//ディレクトリのレデュース処理
function DirectoryCreateReduce(state, action){
  return {
    // /data:newdata,
    message:'Added!',
    mode:'default',
    fdata:[],
    alertflag:true
  };
}

function DirectoryDletedeleteReduce(state, action){
  let newdata = state.data.slice();
  newdata.splice(action.index, 1);
  return {
    data:newdata,
    message:'delete "' + action.index + '":',
    mode:'delete',
    fdata:[]
  }
}

function URLAddReduce(state, action){
  let f = action.find;
  let fdata = [];
  state.data.forEach((value)=>{
    if (value.message.indexOf(f) >= 0){
      fdata.push(value);
    }
  });
  return {
    data:state.data,
    message:'find "' + f + '":',
    mode:'find',
    fdata:fdata,
  };
}

